.CartBlock {
   color: #fff;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;
   color: #fff;
   width: 700px;
   height: 100%;
   line-height: 40px;
   
}

.CartItem {
   flex: 0 0 100%;
   background-color: rgba(0, 0, 0, 0.25);
   border: 1px solid rgba(255, 255, 255, 0.275);
   display: flex;
   justify-content: space-between;
   height: 50px;
   margin: 0px 0px 25px 0px;
}

.CartItem-Img {
   flex: 0 0 20%;
   img {
      width: 100%;
      height: 100%;
      object-fit: contain;
   }
}

.CartItem-Title {
   flex: 0 0 30%;
}

