.Item {
   background-color: rgba(0, 0, 0, 0.25);
   border: 1px solid rgba(255, 255, 255, 0.275);
   position: relative;
   padding: 5px 0px 15px 0px;
   backdrop-filter: blur(5px);

   text-align: center;
   color: #fff;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   height: 90%;

   &:hover {
      border: rgba(4, 150, 69, 0.967) 1px solid;
      background-color: rgba(0, 0, 0, 0.45);
   }
}

.Item-Img {
   margin: 0 auto;
   width: 50%;
   height: 45%;

   img {
      width: 100%;
      height: 100%;
      object-fit: contain;
   }
}

.Item-Title {
   text-transform: uppercase;
   font-size: 18px;
}

.Item-Sizes {
   display: flex;
   width: 60%;
   margin: 0 auto;
   justify-content: center;
}

.Item-Size {
   border: 1px solid rgba(204, 204, 204, 0.522);
   width: 30px;
   height: 27px;
   margin: 5px;
   cursor: pointer;
}
.Item-Size.Active {
   border: 1px solid rgba(204, 204, 204, 0.522);
   background-color: rgb(175, 169, 169);
}

.Item-Actions {
}

.Item-Btn {
   margin: 5px;
   padding: 5px;
   border: 0;
   border-radius: 5px;
   color: #fff;
   cursor: pointer;
   text-transform: uppercase;
}

.Item-Add {
   background-color: rgba(6, 238, 110, 0.186);
}

.Item-Remove {
   background-color: rgba(0, 0, 0, 0.261);
   color: #ccc;
}

.Item-Cart {
   position: absolute;
   top: 5px;
   left: 5px;
   border-radius: 100%;
   display: block;
   width: 35px;
   height: 35px;
   text-align: center;
   line-height: 35px;
   background-color: green;
   color: #fff;
}
