.SideBar {
   width: 110px;
   height: 100%;
   position: fixed;
   right: 0;
   top: 0;
   background-color: rgba(0, 0, 0, 0.25);
   display: flex;
   justify-content: space-between;
   flex-direction: column;

   span {
      position: absolute;
      top: 5px;
      left: 5px;
      border-radius: 100%;
      display: block;
      width: 35px;
      height: 35px;
      text-align: center;
      line-height: 35px;
      background-color: green;
      color: #fff;
   }
}
.Sidebar-Cart {
   width: 110px;
   height: 110px;
   background-color: #ff4a11;
   display: flex;
   justify-content: center;
   align-items: center;
   svg {
      cursor: pointer;
   }
   svg:hover {
      transition: all 1.3s ease;
      path{
         transition: all .1s ease;
         fill: #ccc;
      }
      
   }
}
.Sidebar-Arrow {
   width: 110px;
   height: 75px;
   background-color: #ff4a11;
}
