.Header {
   width: calc(100% - 110px);
   color: #fff;
   display: flex;
   align-items: center;
   padding: 20px 0px 0px 100px;
   position: fixed;
   top: 0;
   left: 0;
}

.Header-Nav{
   display: flex;
   padding: 0px 0px 0px 50px;

}

.Header-item{
   margin: 0px 46px 0px 0px;
   cursor: pointer;
   transition: color .1s ease;
   &:hover {
      color: #ccc;
   }
}